import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';

import { User } from './user';
//import { USERS } from "./mock-users";

const httpOptions = {
    headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private usersUrl = 'http://127.0.0.1:2222/playermgmt/players/allplayers';

  constructor(
      private http: HttpClient) {}

    getUsers (): Observable<User[]> {
        let a = this.http.get<User[]>(this.usersUrl)
        console.log(a) 
        return a
            .pipe(
                // tap(users => this.log(`fetched users`)),
                catchError(this.handleError('getUsers', []))
            );
    }

  // getUser(id: number): Observable<User> {
  //   return of(USERS.find(user => user.id === id));
  // }

  getUser(id:number): Observable<User> {
      const url = `${this.usersUrl}/${id}`;
      return this.http.get<User>(url).pipe(
          //tap(_ => this.log(`fetched user id={$id}`)),
          catchError(this.handleError<User>(`getUsers id={$id}`))
      );
  }

//Selleks, et saaks bannimist lisada
    updateUser (user: User): Observable<any> {
        return this.http.put(this.usersUrl, user, httpOptions).pipe(
            //tap(_ => this.log(`updated hero id=${hero.id}`)),
            catchError(this.handleError<any>('updateUser'))
        );
    }

    searchUsers (term: string): Observable<User[]> {
      if (!term.trim()) {
          return of ([]);
      }
      return this.http.get<User[]>(`${this.usersUrl}/?nickname=${term}`).pipe(
          catchError(this.handleError<User[]>('searchUsers', []))
      );
    }

  private handleError<T>(operation = 'operation', result?: T) {
      return (error:any): Observable<T> => {
          console.error(error);
          //this.log(`${operation} failed: ${error.message}`);
          return of (result as T);
      };
  }
}
